body {
  background: #dce0e4;
}

.Home {
    font-family: 'Source Sans Pro', sans-serif;
    margin: 70px 7px 0;
    color: #333;
  }

#btn-front {
  padding: 4px;
  border-radius: 50px;
  font-weight: bold;
}

.credits {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: black;
  background:rgba(245, 245, 245, 0.603);
  margin-top: 40px;
}

.credits a {
  color:#322f83;
  background: none;
}
  
  .main-ui {
    max-width: 800px;
    margin: 0 auto;
  }
  
  .our-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .our-form > button {
    padding: 7px;
    border-radius: 0 0 50px 50px;
    border-top: none;
    color: white;
    background-color: #00275a;
    font-weight: bold;
    letter-spacing: 1px;
    width: 150px;
  }

  .our-field {
    padding: 7px;
    text-align: center;
    width: 200px;
    border-radius: 50px;
  }
  
  .status {
    text-align: center;
    font-size: .85rem;
  }
  
  .boxes {
    display: flex;
    width: 100%;
  }
  
  .progress {
    border: 1px solid #c7c7c7;
    border-right: none;
    position: relative;
  }
  
  .progress-inner {
    position: absolute;
    top: 0;
    bottom: 0;
    width: 100%;
    background-color: #7ecc00;
    opacity: .57;
    transform: scaleX(0);
    transform-origin: center left;
    transition: transform .4s ease-out;
  }
  
  .box {
    height: 40px;
    border-right: 1px solid #c7c7c7;
    flex: 1;
  }
  
  .overlay {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(255, 255, 255, .82);
    display: flex;
    justify-content: center;
    align-items: center;
    opacity: 0;
    visibility: hidden;
    transition: all .33s ease-out;
    transform: scale(1.2);
  }
  
  .overlay-inner {
    text-align: center;
    max-width: 700px;
  }
  
  body.overlay-is-open .overlay,
  .overlay--visible {
    opacity: 1;
    visibility: visible;
    transform: scale(1);
  }
  
  body.overlay-is-open .main-ui,
  .blurred {
    filter: blur(4px);
  }
  
  @keyframes showError {
    50% {
      color: red;
      transform: scale(1.2);
    }
  
    100% {
      color: #333;
      transform: scale(1);
    }
  }
  
  .animate-wrong {
    animation: .45s showError;
  }
  
  .problem {
    font-size: 5rem;
    margin: 0;
    text-align: center;
  }
  
  .end-message {
    font-size: 1.5rem;
    margin-top: 0;
  }
  
  .reset-button {
    font-size: 1.2rem;
    background-color: #004094;
    border: none;
    color: #FFF;
    border-radius: 7px;
    padding: 12px 20px;
    display: inline-block;
    outline: none;
    cursor: pointer;
  }
  
  .reset-button:hover {
    background-color: #00367e;
  }
  
  .reset-button:focus {
    background-color: #00275a;
  }